<template>
  <div class="seePage">
    <!-- 采购计划列表查看 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item>采购管理</el-breadcrumb-item>
      <el-breadcrumb-item class="attrbute">
        <span @click="goBack">厂家往来/返利</span>
      </el-breadcrumb-item>
      <el-breadcrumb-item>厂家返利账户</el-breadcrumb-item>
    </el-breadcrumb>
    <div class="search_container">
      <el-form :inline="true" :model="params" class="demo-form-inline">
        <el-form-item label="采购组织：" size="mini">
          {{this.rebatedetailIndexGet.company_name}}
        </el-form-item>
        <el-form-item label="供应商(厂家)：" size="mini">
          {{this.rebatedetailIndexGet.supplier_name}}
        </el-form-item>
        <el-form-item label="品牌" size="mini" label-width="110px">
          <getBrand v-model='params.brand_id' />
        </el-form-item>
        <el-form-item label="业务日期" size="mini">
          <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
        <el-form-item size="mini" class="search-style search">
          <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
          <el-button plain @click="onReset" size="mini">重置</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div class="table_container">
      <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" style="width: 100%" max-height="600">
        <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="document_type" label="单据类型">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="rebate_price" label="返利金额">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="use_rebate_price" label="使用返利" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="rebate_balance" label="余额" align="right">
        </el-table-column>
        <el-table-column :show-overflow-tooltip="true" property="business_sn" label="单据号">
        </el-table-column>
      </el-table>
    </div>
    <div class="page_container">
      <div class="pageNumber">每页显示</div>
      <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters } from "vuex";
import { getData } from "common/utils/public_function/commonUtils";
export default {
  name: "purchaseRebatedetailSee",
  data() {
    return {
      authority: {},
      TableData: {
        tableData: [],
      },
      timeValue: [],
      searchParams: {},
      company_id: {},
      supplier_id: {},
      params: {},
      loading: true,
      brandsList: [], //品牌管理相关
      list: [], //品牌管理相关
      goPage: 1,
      per_page: [30, 50, 100, 500],
      pageSize: 30,
    };
  },
  computed: {
    ...mapGetters(["rebatedetailIndexGet"]),
  },
  methods: {
    ...mapActions(["rebatedetailIndexACT"]),
    onReset() {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$set(this, "searchParams", {
        params: {
          supplier_id: this.supplier_id,
          company_id: this.company_id,
        },
        TableData: this.TableData, //列表数据
        API: "getRebateDetailInfo", //接口名
        vm: this, //传递的this
      });
      getData(this.searchParams);
    },
    onSearch() {
      if (this.timeValue && this.timeValue.length == 2) {
        this.params.start_time = this.timeValue[0];
        this.params.end_time = this.timeValue[1];
      } else {
        this.params.start_time = this.params.end_time = "";
      }
      Object.assign(this.searchParams.params, this.params, {
        page: 1,
      });
      getData(this.searchParams);
    },
    handleSizeChange(val) {
      Object.assign(this.searchParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.searchParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.searchParams.params, {
        page: val,
      });
      getData(this.searchParams);
    },
    goBack() {
      this.rebatedetailIndexACT({
        goPage: this.rebatedetailIndexGet.goPage,
        params: this.rebatedetailIndexGet.params,
        pageSize: this.rebatedetailIndexGet.pageSize,
      });
      this.$router.push({ path: "/purchase/rebatedetail" });
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    /** 采购计划详情 */
    this.supplier_id = this.rebatedetailIndexGet.supplier_id;
    this.company_id = this.rebatedetailIndexGet.company_id;
    this.$set(this, "searchParams", {
      params: {
        supplier_id: this.supplier_id,
        company_id: this.company_id,
      },
      TableData: this.TableData, //列表数据
      API: "getRebateDetailInfo", //接口名
      vm: this, //传递的this
    });
    getData(this.searchParams);
    // this.$axios({
    //   method: "get",
    //   url: this.$api.getRebateDetailInfo,
    //   params: {
    //     supplier_id: this.supplier_id,
    //     company_id: this.company_id
    //   }
    // }).then(res => {
    //   if (res.data.code == 200) {
    //     this.TableData = res.data.data.data;
    //     console.log(res.data.data);
    //   }
    // });
  },
};
</script>

